<template>
  <div class="centerContainer">
    <div class="timelineContainer">
      <div class="timeLineToggler">
        <b-button
          v-if="!isPaginatorShowing"
          class="shadow primaryBg border-0"
          pill
          variant="secondary"
          @click="toggleTimeline"
          v-b-tooltip.hover.right
          title="Show timeline"
          ><i class="icon fa fa-calendar-alt" aria-hidden="true"></i
        ></b-button>
        <b-button
          v-else
          id="timeLineCloser"
          class="shadow"
          pill
          variant="outline-danger"
          @click="toggleTimeline"
          v-b-tooltip.hover.right
          title="Hide timeline"
          ><i class="icon fa fa-times" aria-hidden="true"></i
        ></b-button>
      </div>
      <transition name="fade">
        <div class="timelinePaginator" v-show="isPaginatorShowing">
          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            class="mt-4"
            page-class="date-link"
            first-number
            last-number
            align="fill"
            @change="switchDate"
          >
            <template #prev-text
              ><span><i class="fas fa-angle-left"></i></span
            ></template>
            <template #next-text
              ><span><i class="fas fa-angle-right"></i></span
            ></template>
            <template v-slot:page="{ page, active }">
              <div class="h-100 d-flex flex-column justify-content-around">
                <div class="d-flex align-items-center justify-content-center">
                  <div class="w-100 diamondContainer">
                    <i
                      class="diamond fas fa-square"
                      :class="{
                        active: active === true,
                      }"
                    ></i>
                  </div>
                </div>
                <b v-if="active">{{ datesArray[page - 1] }}</b>
                <i v-else>{{ datesArray[page - 1] }}</i>
              </div>
            </template>
          </b-pagination>
        </div>
      </transition>
      <div class="timelineDropdown">
        <b-dropdown text="Dates" dropleft variant="dark">
          <b-dropdown-item
            href="#"
            v-for="(date, index) in datesArray"
            :key="index"
            @click="switchDateFromDropDown(date)"
            :class="{
              selectedDate: date === currentDate,
            }"
            ><span
              :class="{
                selectedDate: date === currentDate,
              }"
              >{{ date }}</span
            ></b-dropdown-item
          >
        </b-dropdown>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "timeline",
  data() {
    return {
      perPage: 1,
      currentPage: 1,
      isPaginatorShowing: true,
    };
  },
  computed: {
    datesArray: function() {
      return this.$store.state.projectStore.datesArray;
    },
    rows: function() {
      return this.datesArray.length ? this.datesArray.length : 0;
    },
    currentDate: function() {
      let currentDate = this.$store.state.airmapStore.currentDate;
      return currentDate ? currentDate : "";
    },
  },
  methods: {
    switchDate(event) {
      let selectedDate = this.datesArray[event - 1];
      this.$store.dispatch("projectStore/switchProjectbyDate", selectedDate);
    },
    switchDateFromDropDown(date) {
      if (date) {
        this.$store.dispatch("projectStore/switchProjectbyDate", date);

        //Since paginator and the dropdown are independent one won't change based on other's changes
        //therefore explicitly setting current page of the paginator from dropdown
        let selectedDateIndex = this.datesArray.indexOf(date);
        this.currentPage = selectedDateIndex + 1;
      }
    },
    toggleTimeline() {
      this.isPaginatorShowing = !this.isPaginatorShowing;
    },
  },
  mounted() {
    setTimeout(() => {
      //after 5s minimize the timeline
      this.isPaginatorShowing = false;
    }, 5000);
  },
};
</script>

<style scoped>
.centerContainer {
  display: flex;
  justify-content: center;
}
.timeLineToggler {
  text-align: center;
}
.timeLineToggler #timeLineCloser.btn {
  background-color: #ffffff8a;
}
.timeLineToggler #timeLineCloser.btn:hover {
  background-color: var(--danger);
}
.timelineContainer {
  position: absolute;
  top: 50px;
  margin: auto;
  z-index: 999;
  display: block;
}
.timelinePaginator {
  display: flex;
  justify-content: center;
}
.timelineDropdown {
  display: none;
}
li.page-item,
button.page-link {
  height: 2rem !important;
}
.diamond {
  transform: rotate(45deg);
  font-size: 1.1rem;
}
.diamond.active {
  color: var(--primaryBg);
  z-index: 9999999;
}
.diamondContainer {
  position: relative;
  z-index: 999;
  margin-top: 1rem;
}
.diamondContainer:after {
  content: "";
  position: absolute;
  width: 125%;
  height: 0.5rem;
  background-color: var(--dark);
  top: 23%;
  left: 50%;
  z-index: 10;
  transition: all ease-in-out 0.3s;
}

.timeline li.active-tl {
  color: #555555;
}
.timeline li.active-tl:before {
  background: #4caf50;
  color: #f1f1f1;
}

.timeline li.active-tl + li:after {
  background: #4caf50;
}

@media only screen and (max-width: 1026px) {
  .timelinePaginator {
    display: none;
  }
  .timeLineToggler {
    display: none;
  }
  .timelineDropdown {
    display: block;
  }
  .centerContainer {
    justify-content: flex-end;
  }

  .selectedDate {
    background-color: var(--primaryBg);
    color: var(--light) !important;
  }
  .selectedDate a:hover {
    color: var(--dark) !important;
  }
}
</style>
